body {
  background-color: #0c2033;
  font-family: 'Open Sans', sans-serif;
  color: #fff;
  letter-spacing: .5px;
  font-size: 16px;
  font-weight: 300;
}

.percent-raised {
  color: #3cd483; 
}

.percent-fallen {
  color: #d64d96;
}

.loading-container {
  width: 100%;
  text-align: center;
  margin: 40px auto;
}

.error {
  width: 100%;
  margin: 40px 0;
  text-align: center;
  color: #d64d96;
}

/* Remove close icon from input on IE */
input::-ms-clear {
  display: none;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Search {
  position: relative;
  width: 30%;
  height: 35px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (max-width: 700px) {
  .Search {
    width: 100%;
  }
}

.Search-icon {
  z-index: 1;
  position: absolute;
  top: 9px;
  left: 28px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 18px;
  height: 18px;
}

.Search-input {
  box-sizing: border-box;
  background-color: #1f364d;
  border-radius: 4px;
  border: 0;
  padding-left: 35px;
  color: white;
  opacity: .8;
  -webkit-transition: opacity .2s;
  transition: opacity .2s;
  width: 100%;
  height: 35px;
}

.Search-input:focus {
  outline: none;
  opacity: 1;
}

.Search ::-webkit-input-placeholder {
  color: #9cb3c9;
  opacity: 1;
}

.Search ::-ms-input-placeholder {
  color: #9cb3c9;
  opacity: 1;
}

.Search ::placeholder {
  color: #9cb3c9;
  opacity: 1;
}

.Search-loading {
  position: absolute;
  top: 9px;
  right: 28px;
}

.Search-result-container {
  position: relative;
  width: 100%;
  max-height: 299px;
  overflow-y: auto;
  background-color: #0f273d;
  border: 1px solid #0c2033;
  border-radius: 4px;
  box-shadow: 0px 0px 40px 0px#1f364d;
  margin-top: 10px;
}

.Search-result {
  color: #9cb3c9;
  padding: 15px 0 15px 35px;
  border-bottom: 2px solid #0c2033;
  cursor: pointer;
}

.Search-result:hover {
  color: #fff;
}

.Search-no-result {
  color: #9cb3c9;
  padding: 15px 0 15px 35px;
  border-bottom: 1px solid #0f273d;
}
.Header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: #0f273d;
    width: 100%;
    height: 80px;
  }
  
  .Header-logo {
    position: absolute;
    top: 30px;
    left: 20px;
    width: 90px;
  }
  
  @media (max-width: 700px) {
    .Header-logo {
      display: none;
    }
  }
.Table-container {
    overflow-x: auto; /* Needed for table to be responsive */
  }
  
  .Table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
  }
  
  .Table-head {
    background-color: #0c2033;
  }
  
  .Table-head tr th {
    padding: 10px 20px;
    color: #9cb3c9;
    text-align: left;
    font-size: 14px;
    font-weight: 400;
  }
  
  .Table-body {
    text-align: left;
    background-color: #0f273d;
  }
  
  .Table-body tr td {
    padding: 24px 20px;
    border-bottom: 2px solid #0c2033;
    color: #fff;
    cursor: pointer;
  }
  
  .Table-rank {
    color: #9cb3c9;
    margin-right: 18px;
    font-size: 12px;
  }
  
  .Table-dollar {
    color: #9cb3c9;
    margin-right: 6px;
  }
.NotFound {
    width: 100%;
    text-align: center;
    margin-top: 60px;
  }
  
  .NotFound-title {
    font-weight: 400;
    color: #9cb3c9;
  }
  
  .NotFound-link {
    display: inline-block;
    margin-top: 40px;
    color: #fff;
    text-decoration: none;
    border: 1px solid #9cb3c9;
    border-radius: 4px;
    padding: 18px;
    -webkit-transition: border .2s;
    transition: border .2s;
  }
  
  .NotFound-link:hover {
    border: 1px solid #fff;
  }
